@import '@dpe/base/src/global';

// Accordion
//
// Package: `@dpe/accordion`
//
// Accordions use the [Bootstrap Collapse](https://getbootstrap.com/docs/4.1/components/collapse/)
// component with custom styling.
//
// ### Bootstrap javascript:
//
// Requires: `/js/collapse.js`\
// Dependencies:
// * `/js/jquery.min.js`
// * `/js/util.js`
//
// Usage: See [Bootstrap documentation](https://getbootstrap.com/docs/4.1/components/collapse/)
//
// Markup: accordion.twig
//
// Style guide: 7.accordion

.accordion {
  border-bottom: $border-style;

  &:first-of-type {
    border-top: $border-style;
  }

  + *:not(.accordion) {
    margin-top: $paragraph-margin-bottom;
  }

  &-title {
    font-size: inherit;
    max-width: none;
    margin: 0;

    button {
      @include icon('minus');
      background-position: right center;
      background-color: transparent;
      border: 0;
      width: 100%;
      display: block;
      padding: $spacer-m $spacer-l $spacer-m 0;
      transition: color .2s ease-in-out;
      cursor: pointer;
      text-align: left;

      &.collapsed {
        background-image: url('../images/plus.svg');
      }

      &:hover {
        text-decoration: underline;
        color: $link-hover-color;
      }
    }
  }

  &-body {
    border-top: $border-style;
    padding: $spacer-ml 0;

    > :last-child {
      margin-bottom: 0;
    }
  }
}
