// scss-lint:disable ColorKeyword, ColorVariable, SpaceAfterComma

// Functions
//
// All of the bootstrap functions, plus any custom ones.
// For a full list of functions see https://github.com/twbs/bootstrap/blob/v4-dev/scss/_functions.scss

@import "bootstrap/scss/functions";

// Colors
//
// See [Bootstrap maps and loops](https://getbootstrap.com/docs/4.1/getting-started/theming/#maps-and-loops)
// for documentation on overriding the colour variables.
//
// Colors:
// $primary:          #002664 - Links, buttons and dark backgrounds
// $primary-light:    #e5e9ef - Alternative backgrounds
// $secondary:        #d7153a - Secondary buttons and errors
// $secondary-light:  #fbe7eb - Error message background
// $foreground:       #0d121f - Text colour
// $foreground-rev:   #ffffff - Reversed text colour, when used on dark backgrounds
// $foreground-light: #4a4e57 - Muted text colour
// $foreground-alt:   #002664 - Alternative text colour
// $background:       #ffffff - Background colour
// $background-rev:   #002664 - Reversed background colour (dark)
// $background-alt:   #e5e9ef - Alternative background colour
// $border-color:     #e6e6e6 - Border colour (except for inputs)
// $input-placeholder-color: #6f6f6f - Lightest allowed text colour on a white background
// $orange:           #f78f1e - Warning message colour
// $orange-light:     #fef3e8 - Warning message background
// $green:            #43a047 - Success message colour
// $green-light:      #edf6ed - Success message background
//
// Style guide: 1.colours

$white:     #fff !default;
$gray-050:  #f3f3f4 !default;
$gray-100:  #e6e6e6 !default;
$gray-250:  #c2c4c7 !default;
$gray-500:  #7f7f7f !default;
$gray-600:  #6f6f6f !default; // lightest for text
$gray-750:  #4a4e57 !default;
$black:     #0d121f !default;

$grays: (
    "050": $gray-050,
    "250": $gray-250,
    "750": $gray-750,
);

$blue:          #002664 !default;
$blue-light:    #e5e9ef !default;
$red:           #d7153a !default;
$red-light:     #fbe7eb !default;
$orange:        #f78f1e !default;
$orange-light:  #fef3e8 !default;
$green:         #43a047 !default;
$green-light:   #edf6ed !default;

$white-5:       rgba($white, .5);
$black-5:       rgba($black, .5);
$black-7:       rgba($black, .7);

$colors: (
    "blue-light":     $blue-light,
    "red-light":      $red-light,
    "organge-light":  $orange-light,
    "green-light":    $green-light,
    "gray-dark":      $gray-750,
    "white-5":        $white-5,
    "black-5":        $black-5,
    "black-7":        $black-7,
);

$primary:                $blue!default;
$primary-light:          $blue-light!default;
$secondary:              $red !default;
$secondary-light:        $red-light !default;
$foreground:             $black !default;
$foreground-rev:         $white !default;
$foreground-light:       $gray-750 !default;
$foreground-alt:         $primary !default;
$background:             $white !default;
$background-rev:         $primary !default;
$background-alt:         $primary-light !default;
$translucent-highlight:  $white-5 !default;
$translucent-shadow:     $black-5 !default;
$translucent-overlay:    $black-7 !default;
$border-color:           $gray-100 !default;
$light:                  $background-alt !default;
$dark:                   $background-rev !default;

$yiq-text-dark:          $black !default;
$yiq-text-light:         $white !default;

// Fonts
//
// The Gotham webfonts are loaded from `https://cloud.typography.com/6161932/7684812/css/fonts.css` and are
// available for use on certain whitelisted domains. System fonts (like this) are used as the fallback.
//
// The regular (400) weight uses Gotham Book and is available in normal and italic.
// The bold (700) weight uses Gotham Bold and is only available in normal.
//
// ### Font variables:
// | Name                      | Value  |
// | ------------------------- | ------ |
// | $font-family-base         | 'Gotham A', 'Gotham B', $font-family-sans-serif |
// | $font-weight-normal       | 400   |
// | $font-weight-bold         | 700   |
//
// ### Font example:
// <p class="kss-font-demo">Gotham Normal and
// <span class="kss-font-demo__b">Bold</p>
//
// Style guide: 1.fonts

$font-family-sans-serif:      -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"!default;
$font-family-base:            'Gotham A', 'Gotham B', $font-family-sans-serif !default;

$font-weight-normal:          400 !default;
$font-weight-bold:            700 !default;

// Typography
//
// See [Bootstrap typography](https://getbootstrap.com/docs/4.1/content/typography/) for documentation.
//
// ### Size breakdown*:
// All pixel units are converted to `rem`
//
// | Name          | Size | Leading | Weight | Example    |
// | ------------- | ---- | ------- | ------ | ---------- |
// | heading-xxl   | 48px | 1.1     | bold   | h1         |
// | heading-xl    | 40px | 1.1     | medium |            |
// | heading-l     | 32px | 1.1     | medium | h2         |
// | heading-m     | 24px | 1.2     | medium | h3         |
// | heading-s     | 18px | 1.2     | medium | h4         |
// | heading-xs    | 16px | 1.2     | medium | h5         |
// | heading-xxs   | 14px | 1.2     | medium | h6         |
// | body-xxl      | 32px | 1.4     | normal |            |
// | body-xl       | 24px | 1.6     | normal | blockquote |
// | body-l        | 20px | 1.6     | normal | lede       |
// | body-m        | 18px | 1.6     | normal |            |
// | body-s        | 16px | 1.6     | normal | default    |
// | body-xs       | 14px | 1.6     | normal | caption    |
// | body-xxs      | 12px | 1.6     | normal |            |
// | ui-xxl        | 32px | 1       | normal |            |
// | ui-xl         | 24px | 1       | normal |            |
// | ui-l          | 20px | 1       | normal |            |
// | ui-m          | 18px | 1       | normal |            |
// | ui-s          | 16px | 1       | normal | forms      |
// | ui-xs         | 14px | 1       | normal |            |
// | ui-xxs        | 12px | 1       | normal |            |
//
// ### Responsive sizing
//
// Responsive sizing is done on a per element basis. The standard pattern
// is to increase by one size on desktop for all body and heading sizes.
// The example column refers to the desktop size.
//
// ### Typographic utility classes
//
// All heading and body sizes have a corresponding class that can be used
// on any element. The class name structure follows the same pattern as
// the names here, only with a 2 dashes instead of 1. So `.heading--xxl`
// or `.body--xs` for example. These class have the responsive sizing
// baked into them.
//
// Style guide: 1.typography

$font-size-base: 1rem !default;
$spacer:         $font-size-base !default;

// Better variable names for typography.
$heading-xxl:    $font-size-base * 3 !default; // h1
$heading-xl:     $font-size-base * 2.5 !default;
$heading-l:      $font-size-base * 2 !default; // h2
$heading-m:      $font-size-base * 1.5 !default; // h3
$heading-s:      $font-size-base * 1.125 !default; // h4
$heading-xs:     $font-size-base !default; // h5
$heading-xxs:    $font-size-base * .875 !default; // h5

$text-xxl:       $font-size-base * 2 !default;
$text-xl:        $font-size-base * 1.5 !default; // blockquote
$text-l:         $font-size-base * 1.25 !default; // lede
$text-m:         $font-size-base * 1.125 !default;
$text-s:         $font-size-base !default; // default, input, buttons, label
$text-xs:        $font-size-base * .875 !default; // small, caption
$text-xxs:       $font-size-base * .75 !default;

// Bootstrap variables.
$line-height-base:        1.6 !default;
$line-height-util:        1 !default;

$headings-font-weight:    $font-weight-bold !default;
$headings-line-height:    1.2 !default;

$h1-font-size:            $heading-xxl !default;
$h2-font-size:            $heading-l !default;
$h3-font-size:            $heading-m !default;
$h4-font-size:            $heading-s !default;
$h5-font-size:            $heading-xs !default;
$h6-font-size:            $heading-xxs !default;

$font-size-lg:            $text-l !default;
$font-size-sm:            $text-xs !default;
$font-size-xs:            $text-xxs !default;

$lead-font-size:          $text-l !default;
$lead-font-weight:        $font-weight-normal !default;
$small-font-size:         $text-xs !default;
$text-muted:              $foreground-light !default;
$blockquote-small-color:  $foreground-light !default;
$blockquote-font-size:    $text-l !default;
$hr-border-color:         $border-color !default;

// Breakpoints
//
// See [Bootstrap breakpoints](https://getbootstrap.com/docs/4.1/layout/overview/#responsive-breakpoints)
// for full documentation.
//
// As we take a mobile first approach, the most common use is via the `media-breakpoint-up()`
// mixin with one of the named breakpoints below.
//
// ### Breakpoint sizes:
// | Name  | Size      |
// | ----- | --------- |
// | sm    | 400px+    |
// | md    | 600px+    |
// | lg    | 900px+    |
// | xl    | 1200px+   |
// | xxl   | 1600px+   |
//
// Style guide: 1.mixins.breakpoints

$grid-breakpoints: (
    xs: 0,
    sm: 400px,
    md: 600px,
    lg: 900px,
    xl: 1200px,
    xxl: 1600px
);

// Variables
//
// All of the bootstrap variables, with below custom overrides.
// For a full list of variables see
// [Bootstraps _variables.scss file](https://github.com/twbs/bootstrap/blob/v4-dev/scss/_variables.scss)
//
// Spacing is based off of an 8px (.5rem) baseline. All vertical and horizontal
// spacing should follow suit.
//
// The numbered variables below are referenced in the Spacing utility class.
//
// ### Spacing variables:
// | Name                      | Value  |
// | ------------------------- | ------ |
// | $spacer-xs (1)            | 4px    |
// | $spacer-s (2)             | 8px    |
// | $spacer-sm                | 12px   |
// | $spacer-m (3)             | 16px   |
// | $spacer-ml                | 24px   |
// | $spacer-l (4)             | 32px   |
// | $spacer-xl (5)            | 48px   |
// | $spacer-xxl               | 56px   |
// | $spacer-xxxl              | 64px   |
// | $container-width-xl       | 1184px |
// | $container-width-lg       | 864px  |
// | $container-width-md       | 768px  |
// | $container-width-sm       | 512px  |
// | $line-length              | 768px  |
//
// Style guide: 1.variables

// Add some nicer spacing variables.
$spacer-xs:                ($spacer * .25) !default;
$spacer-s:                 ($spacer * .5) !default;
$spacer-sm:                ($spacer * .75) !default;
$spacer-m:                 $spacer !default;
$spacer-ml:                ($spacer * 1.5) !default;
$spacer-l:                 ($spacer * 2) !default;
$spacer-xl:                ($spacer * 3) !default;
$spacer-xxl:               ($spacer * 3.5) !default;
$spacer-xxxl:              ($spacer * 4) !default;

$spacers: (
    1: $spacer-xs,
    2: $spacer-s,
    3: $spacer-m,
    4: $spacer-l,
    5: $spacer-xl,
);

$container-width-sm:  ($spacer * 32) !default;
$container-width-md:  ($spacer * 48) !default;
$container-width-lg:  ($spacer * 54) !default;
$container-width-xl:  ($spacer * 74) !default;

$line-length: $container-width-md !default;

$container-max-widths: (
    sm: $container-width-sm,
    md: $container-width-md,
    lg: $container-width-lg,
    xl: $container-width-xl
);

// Body
$body-bg:     $background !default;
$body-color:  $foreground !default;

$headings-margin-bottom:  $spacer-ml !default;
$paragraph-margin-bottom: $spacer-l !default;
$grid-gutter-width:       $spacer-l !default;

$line-height-lg:          1.6 !default;
$line-height-sm:          1.6 !default;

$border-radius-sm:        2px !default;
$border-radius:           3px !default;
$border-radius-lg:        6px !default;

$border-width:            1px !default;
$border-style:            $border-width solid $border-color !default;

$box-shadow-sm:           0 1px 1px $black-5 !default;
$box-shadow:              0 3px 3px $black-5 !default;
$box-shadow-lg:           0 6px 6px $black-5 !default;

$caret-width:             $spacer-xs !default;
$list-inline-padding:     $spacer-m !default;

// Tables
$table-cell-padding:          $spacer-sm !default;
$table-cell-padding-sm:       $spacer-s !default;

$table-border-color:          $border-color !default;
$table-head-bg:               $background-alt !default;
$table-head-color:            $foreground !default;
$table-dark-bg:               $background-rev !default;
$table-dark-border-color:     lighten($background-rev, 7.5%) !default;

// Inputs and Buttons
$input-btn-padding-y:         $spacer-s !default;
$input-btn-padding-x:         $spacer-m !default;
$input-btn-padding-y-sm:      $spacer-xs !default;
$input-btn-padding-x-sm:      $spacer-s !default;
$input-btn-padding-y-lg:      $spacer-sm !default;
$input-btn-padding-x-lg:      $spacer-m !default;
$input-btn-border-width:      2px !default;

// Buttons
$btn-font-weight:             $font-weight-bold !default;
$btn-block-spacing-y:         $spacer-s !default;
$btn-border-radius:           $spacer-l !default;
$btn-border-radius-lg:        $spacer-xl !default;
$btn-border-radius-sm:        $spacer-m !default;
$btn-border-width:            2px !default;

// Inputs
$input-border-color:          $gray-500 !default;
$input-color:                 $black !default;
$input-placeholder-color:     $gray-600 !default;
$input-box-shadow:            none !default;

// Forms
$label-margin-bottom:         $spacer-xs !default;
$form-group-margin-bottom:    $spacer-m !default;

// Links
$link-color:                $primary !default;
$link-decoration:           none !default;
$link-hover-color:          lighten($link-color, 20%) !default;
$link-hover-decoration:     none !default;

// Modal sizes
$modal-xl:                  $container-width-xl !default;
$modal-lg:                  $container-width-lg !default;
$modal-md:                  $container-width-md !default;
$modal-sm:                  300px !default;

// Bootstrap variables come last.
@import "bootstrap/scss/variables";

// Mixins
//
// All of the bootstrap mixins, plus any custom ones.
//
// Style guide: 1.mixins

@import "bootstrap/scss/mixins";

// image-replace()
//
// Modern image replacement technique, available as a mixin only rather
// than as a utility class. Also available as `image-replace-reset()` to undo `image-replace()`
//
// Included since [bootstrap is deprecating `.hide-text`](https://getbootstrap.com/docs/4.1/utilities/image-replacement/)
//
// $display = block - Sometimes what we're replacing can be an `inline-block` element.
//
// Style guide: 1.mixins.image-replace

@mixin image-replace($display: block) {
  display: $display;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

@mixin image-replace-reset {
  text-indent: 0;
  white-space: normal;
  overflow: auto;
}

// icon()
//
// A mixin to include an icon by name. You can also override
// the `background-size` property. eg. `@include icon('alert', 20px);`
// See the [Icons package](/styleguide/section-3.html) for the full name set.
//
// $name - Icon name (filename without the .svg extension)
// $size = FALSE - Set a custom background size property
//
// Style guide: 1.mixins.icons

@mixin icon($name, $size: FALSE) {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('../images/#{$name}.svg');
  @if $size != FALSE {
    background-size: $size;
  }
}
